import React from 'react';
import styles from './buttonGroup.css';
import Button from '../button/button';

export default function ButtonGroup({ data }) {
  return (
    <div className={styles.component}>
      {data.map((i) => (
        <Button href={i.href} secondary={i.secondary}>
          {i.label}
        </Button>
      ))}
    </div>
  );
}
